@font-face {
    font-family: "DejaVuSans-ExtraLight";
    src: url('../../Fonts/DejaVuSans-ExtraLight.ttf');
    font-weight: normal;
    font-style: normal;

}

.navbar {
    width: 100vw;
    background: #303f4b;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    border-top: rgb(22, 22, 22) solid 1.5px;
    border-bottom: rgb(22,22,22) solid 1.5px;
}
.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    max-width: 1800px;
    /* border: red solid 1px; */
}
.nav-menu {
    width: 100%;
    height: 100%;
    display: flex;
    list-style: none;
    text-align: center;
    transition: all 0.3s ease-in-out;
    opacity: 1;
    /* margin-right: 2rem; */
    /* border: yellow solid 1px; */
    justify-content: center;
}
.nav-menu .nav-item .active{
    color: red;
}
.nav-menu .search{
    margin-left: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin-right: 5%;
}
.nav-menu .search-div{
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;  
    /* border-bottom: rgb(160, 160, 160) solid 2px;   */
}
.nav-menu .search img{
    height: 50%;
    margin: 5px;
    display: flex;
    z-index: 999;
    color: white;
}
.nav-menu .search input{
    height: 30px;
    background-color: #405363;
    border: none;
    border-radius: 0;
    color: rgb(228, 226, 226);
    transition: all .4s ease-in-out;
}
.nav-menu .search input::placeholder{
    color: rgb(211, 207, 207);
    padding-left: 8px;
    outline: none;
    font-size: 1rem;
}
.nav-menu .search input:focus{
    outline: none;
    padding-left: 8px;
    font-size: 1rem;
    background-color:#6c89a1 ;
    color: rgb(0, 0, 0);
    
}
.nav-menu .search input textarea{
    padding: 9px;
}


.navbar .nav-container .nav-logo{
    display: flex;
    height: 100%;
    position: relative;
    transition: all .2s ease-in-out;
    opacity: 0;
    /* border: red solid 1px; */
    align-items: center;
    cursor: pointer;
}
.navbar .nav-container .hidden{
opacity: 1;
}
.navbar .nav-container .nav-logo img {
    height:60%;
    position: absolute;
    /* left: -5em; */
    transition: all .1s ease-in-out;
}

/* .nav-menu .nav-item{
    height: 100%;
} */
.nav-item a{
        font-family: "DejaVuSans-ExtraLight"; 
        font-size: 1rem;
        width: 100%;
}
.nav-links {
    
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
    color: white;
    text-decoration: none;
    padding: 0 1rem;
    border-bottom: 2px solid  transparent;
    font-size: 1rem;
}
.nav-links button{
    background-color:rgba(235, 235, 235, .2);
    width: 100px;
    height: 100%;
    border: black solid 1px;
    color: rgb(255, 255, 255);
    border-radius: 3px;
    font-weight: lighter;
    font-size: 1rem;
    margin-left: 40px;
    cursor: pointer;
    transition: all ease-in-out .1s;
    outline: none;
}
.nav-links button:hover{
    background-color: rgb(151, 150, 150);
    transition: all ease-in-out .1s;
}
.main{
    font-family: "", ;
    font-weight: lighter;
    
    /* src: url('../../Fonts/Chartarapet-Regular.otf'); */
    display: flex;
    height: 100%;
    /* padding-top: 100px; */
    color: white;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    border-bottom: 2px solid  transparent;
    font-size: 1rem;
}
.erithay{
    font-weight: 600;
    /* color: rgb(247, 100, 100); */
    text-decoration: none;
}
.login {
    background-color:rgba(235, 235, 235, .2);
    border-radius: 3px;
}
.nav-item span{
    text-overflow: ellipsis;
}

.nav-item span:after {
    content: '';
    display: block;
    height: 2px;
    width: 0;
    background: transparent;
    transition: width 0.3s ease, background-color 0.1s ease;
}

.nav-item span:hover:after {
    width: 100%;
    background: rgb(197, 30, 1);
    
}

/* .nav-item .active {
    color: rgb(158, 158, 158);
} */
.fas .fa-times{
    color: white;
}
.fa-search{
    color: white;
    margin: 5px;
}
.nav-icon {
    
    display: none;
}
.nav-icon img{
    border: red solid 2px;
}
@media only screen and (max-width: 1110px) and (min-width: 950px)  {
    .nav-item a{
        font-family: "DejaVuSans-ExtraLight"; 
        font-size: .8rem;
        width: 100%;
    }
    .nav-menu .search{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
    }
    
}

@media only screen and (max-width: 1250px){
    .nav-menu .search{
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .nav-menu{
        display: flex;
        justify-content: flex-end;
        margin-right: 30px;
    }
}
@media screen and (max-width: 1023px) {
   
    .navbar{
        width: 100vw;
        position: sticky;
        top: 0;
    }
    
    .navbar .nav-container .nav-logo{
        opacity: 0;
        align-items: center;
        /* border: red solid 1px; */
        position: unset;
        justify-content: center;
        display: flex;
        transition: all .1s ease-in-out;
    }
    .nav-menu .search{
        width: 100%;
        margin-left: 0;
    }
    .nav-menu .search-div{
        display: flex;
        width: 600px;
    }
    .nav-links {
        height: 40px;
    }
    .navbar .nav-container .hidden{
        opacity: 1;
    }
    .navbar .nav-container .nav-logo img{
        height: 60%;
         position: unset;
         transition: all .1s ease-in-out;
         
    }
    a .nav-item .nav-links span{
        font-weight: bold;
        color: white;
    }
    .nav-item:hover:after {
        width: 100%;
        background: none;
        color: gray;
    }
    .nav-item a:hover:after {
        color: gray;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        border-top: #fff solid 1px;
        position: absolute;
        top: 48px;
        left: -110%;
        opacity: 1;
        z-index: 999;
        /* transition: all 0.4s ease; */
        /* transition: opacity 0.2s; */
    }
    .nav-menu.active {
        height: 100vh;
        background:#25303a;
        left: 0px;
        opacity: 1;
        transition: all 0.1s ease-in-out;
        z-index: 999;
        align-items: left;
        justify-content: flex-start;
    }
    .nav-icon {
        height: 100%;
        /* border: red solid 1px; */
        display: flex;
        font-size: 2rem;
        position: absolute;
        top: 0;
        right: 20px;
        justify-content: center;
        align-items: center;
    }
    #home{
        margin-top: 20px;
    }
    
    .nav-item {
        display: flex;
        height: 60px;
        line-height: 40px;
        align-items: center;
        justify-content: left;
        padding-left: 30px;
    
    }
    
    .nav-item.active{
        height: 40px;
    }
    .nav-item.active .nav-links .button {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 70px;
    }
    .nav-item.active .nav-links.button .login-button{
        margin: 0;
        
    }
    .nav-links button{
        width: 180px;
        margin-left: 0;
        font-size: 1.2rem;
        font-weight: 100;
        box-shadow: 1px 1px 4px 1px rgb(48, 48, 48) ;
        color: white;
        border-radius: 0;
    }
    .search-div{
        height: 30px;
    }
    .button{
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
    }
    .nav-menu .search{
        display: flex;        
        height: 70px;
        /* margin-top: 60px; */
    }
    .nav-menu .search-div{
        align-items: center;
        justify-content: center;
    }
    .nav-menu .search img{
        height: 50%;
        margin: 5px;
        display: flex;
        z-index: 999;
        color: white;
    }
    #login-button{
        margin-top: 60px;
        display: flex;
        padding: 0;
        align-items: center;
        justify-content: center;
    }
    .nav-menu .search input{
        height: 40px;
        background-color: #4f6679;
        border: none;
        border-radius: 0;
        color: rgb(228, 226, 226);
        width: 250px;
        
    }
    .nav-menu .search input::placeholder{
        color: rgb(211, 207, 207);
        padding-left: 8px;
        outline: none;
        font-size: 1rem;
    }
}

